









import Vue from 'vue';
export default Vue.extend({
  props: {
    caption: {
      type: String,
      required: true,
    },
    large: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
  },
  methods: {
    handleClick() {
      if (this.large) {
        this.$emit('next');
      } else {
        this.$emit('handle-click', this.caption);
      }
    },
  },
});
